<template>
    <div>
        <div class="tem-left">
            <el-tree 
            :data="treeData" 
            :props="defaultProps"
            default-expand-all 
            highlight-current 
            accordion
            @node-click="handleNodeClick">
            <span class="custom-tree-node" slot-scope="{ node, data }">
                <i v-if="data.children" :class="['iconfont', folderIcon]"></i>
                <i v-else :class="modalIcon"></i>
                <span style="margin-left: 5px;">{{ node.label }}</span>
            </span>
            <!-- <template v-slot="{ node }">
                <span class="custom-icon">
                <i class="custom-icon-class" :class="getIconClass(node)"></i>
                </span>
                <span class="custom-label">{{ node.label }}</span>
            </template> -->
            </el-tree>
      </div>
      <!-- 
        data 属性：绑定了一个名为 treeData 的数据源，用于渲染树形结构的数据。
        default-expand-all 属性：设置默认展开所有节点。即在初始化时，所有的节点都会展开显示。
        highlight-current 属性：标识当前选中的节点，为选中节点添加高亮效果。
        props 属性：绑定了一个名为 defaultProps 的对象，用于配置树形结构数据的属性。具体的属性配置会在代码中定义（未在提供的代码中给出）。
        @node-click 事件：绑定了一个名为 handleNodeClick 的方法，用于处理节点点击事件。
       -->
    </div>
</template>

<script>
export default {
    data () {
        return {
            treeData:[
                    {
                        name: "Folder 1",
                        type: "folder",
                        children: [
                        {
                            name: "File 1-1",
                            type: "file"
                        },
                        {
                            name: "File 1-2",
                            type: "file"
                        },
                        {
                            name: "Folder 1-3",
                            type: "folder",
                            children: [
                            {
                                name: "File 1-3-1",
                                type: "file"
                            },
                            {
                                name: "File 1-3-2",
                                type: "file"
                            }
                            ]
                        }
                        ]
                    },
                    {
                        name: "Folder 2",
                        type: "folder",
                        children: [
                        {
                            name: "File 2-1",
                            type: "file"
                        },
                        {
                            name: "Folder 2-2",
                            type: "folder",
                            children: [
                            {
                                name: "File 2-2-1",
                                type: "file"
                            }
                            ]
                        }
                        ]
                    },
                    {
                        name: "File 3",
                        type: "file",
                        children:[]
                    }
                    ],
            defaultProps:{
                children: 'children',
                label: 'name'
            },
              // 文件夹以及模板图标
            folderIcon: 'el-icon-folder',
            modalIcon: 'el-icon-document'
        }
    },
    methods:{
        handleNodeClick(){

        }
    }
}
</script>
 
<style lang = "scss" scoped>
    .custom-tree-node {
    font-size: 12px;
    i {
      font-size: 12px;
    }
    .el-icon-folder {
      color: #ffd300;
      font-weight: bold;
    }
    .el-icon-document {
      color: rgba(0, 0, 0, 0.5);
    }
}

</style>