<template>
  <div class="" ref="divSelect">
    <!-- el-icon-arrow-up -->
    <el-input @click.native="handleClick" v-model="selectValue" readonly>
      <template slot="suffix">
        <i
          class="el-input__icon"
          :class="[!show ? 'el-icon-arrow-down' : 'el-icon-arrow-up']"
        ></i>
      </template>
    </el-input>
    <transition name="drop-down">
      <div ref="popper" v-show="show" class="drop-box">
        <slot />
      </div>
    </transition>
  </div>
</template>

<script>
//1.点击输入框下拉，并且不能编辑
//2.选择一个后菜单隐藏
//3.选中的数据要显示在输入框中
export default {
  props: {
    value: {
      type: [String, Number],
      default: "",
    },
  },
  watch: {
    value: {
      handler(val) {
        if (val) {
          this.selectValue = val;
          this.show = false;
        }
      },
      // immediate: true,
    },
  },
  mounted() {
    let _that = this;
    document.addEventListener("click", function (e) {
      if (_that.$refs.divSelect) {
        if (
          !!_that.$refs.divSelect.contains(e.target) ||
          !!_that.$refs.popper.contains(e.target)
        )
          return;
        else _that.show = false;
      }
    });
  },
  data() {
    return {
      selected: "",
      selectValue: "",
      show: false,
    };
  },
  methods: {
    handleClick() {
      this.show = !this.show;
    },
  },
};
</script>

<style scoped>
 .drop-box {
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  background-color: #fff;
  position: absolute;
  z-index: 99;
  padding: 10px;
}
.drop-down-enter {
  opacity: 0;
  transform: translate(0px, -100px) scaleY(0.3);
}
.drop-down-leave-to {
  opacity: 0;
  transform: translate(0px, -100px) scaleY(0.3);
}
.drop-down-enter-active {
  transition: all 0.28s ease;
}
.drop-down-leave-active {
  transition: all 0.28s ease;
}
</style>
